import { Mark } from '@ali/4ever-cangjie';

interface InlineCode extends Mark {
  type: 'inlineCode';
};

const InlineCode = {
  TYPE: 'inlineCode',

  create(): InlineCode {
    return Mark.create({ type: InlineCode.TYPE }) as InlineCode;
  },

  isInlineCode(maybeInlineCode: any): maybeInlineCode is InlineCode {
    return Mark.isMark(maybeInlineCode) && maybeInlineCode.type === InlineCode.TYPE;
  },
};

export default InlineCode;
